.content{
    display: flex;
    justify-content: space-around;
}
.settings{
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 300;
}
.banner{
    width: 55%;
    align-self: center;
    padding: 8px;
}
@media (max-width:800px){
.content{
    flex-direction: column-reverse;
    align-items: center;
}
.banner{
    width: 85%;
    padding: 0%;
    padding-top: 20px;
}
.settings{
    width: 100%;
}
}
.studentdetail{
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 90%;
    justify-content: space-evenly;
 
}