.quiz{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Times New Roman', Times, serif;
}
.subTitle{
    font-size: 25px;
    border: 1px solid black;
    box-shadow: 4px 4px 2px;
    padding:5px 10px;
}
.quizInfo{
    width: 100%;
    display:flex;
    justify-content: space-between;
    text-transform: uppercase;
    margin: 10px;
}